import React, { Component } from "react";
import Typical from "react-typical";
import Switch from "react-switch";

class Header extends Component {
  titles = [];

  constructor() {
    super();
    this.state = { checked: false };
    this.onThemeSwitchChange = this.onThemeSwitchChange.bind(this);
    this.applyPickedLanguage = this.applyPickedLanguage.bind(this);
  }

  onThemeSwitchChange(checked) {
    this.setState({ checked });
    this.setTheme();
  }

  setTheme() {
    const body = document.body;
    const newTheme = body.getAttribute("data-theme") === "dark" ? "light" : "dark";
    body.setAttribute("data-theme", newTheme);
  }

  applyPickedLanguage(language) {
    this.props.loadResumeFromPath(language); // Call prop function from App.js to switch language
    this.setState({ language }); // Update local state for icon brightness
  }

  render() {
    if (this.props.sharedData) {
      const { name } = this.props.sharedData;
      this.titles = this.props.sharedData.titles.map((x) => [x.toUpperCase(), 1500]).flat();
    }

    const HeaderTitleTypeAnimation = React.memo(() => {
      return <Typical className="title-styles" steps={this.titles} loop={50} />;
    });

    return (
      <header
        id="home"
        style={{
          height: window.innerHeight - 140,
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          backgroundImage: "url(/images/FMY.JPG)", // Replace with your actual image path
          backgroundSize: "cover",
          backgroundPosition: "left center",
          filter: "brightness(20%)" // Low brightness for background
        }}
      >
        <div className="row aligner" style={{ height: "100%" }}>
          <div className="col-md-12" style={{ zIndex: 2 }}> {/* Ensure content is above the background */}
            <div>
              {/* Remove Icon */}
              <h1 className="mb-0" style={{ fontSize: "4.5em", marginTop: "-60px" }}> {/* Increased font size and moved up */}
                <Typical steps={[this.props.sharedData.name]} wrapper="p" />
              </h1>
              <div className="title-container" style={{ marginTop: "-30px" }}> {/* Moved text further up */}
                <HeaderTitleTypeAnimation />
              </div>

              {/* Language Toggle */}
              <div className="language-toggle" style={{ marginTop: "30px", display: "flex", justifyContent: "center", gap: "15px" }}>
                <span
                  onClick={() => this.applyPickedLanguage("en")}
                  className="iconify language-icon"
                  data-icon="twemoji:flag-for-flag-united-states"
                  data-inline="false"
                  style={{
                    cursor: "pointer",
                    fontSize: "2.5em",
                    filter: this.props.language === "en" ? "brightness(50%)" : "none"
                  }}
                ></span>
                <span
                  onClick={() => this.applyPickedLanguage("tr")}
                  className="iconify language-icon"
                  data-icon="twemoji:flag-for-flag-turkey"
                  data-inline="false"
                  style={{
                    cursor: "pointer",
                    fontSize: "2.5em",
                    filter: this.props.language === "tr" ? "brightness(50%)" : "none"
                  }}
                ></span>
              </div>

              <Switch
                checked={this.state.checked}
                onChange={this.onThemeSwitchChange}
                offColor="#baaa80"
                onColor="#353535"
                className="react-switch mx-auto"
                width={90}
                height={40}
                uncheckedIcon={
                  <span
                    className="iconify"
                    data-icon="twemoji:owl"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "20px",
                      color: "#353239",
                    }}
                  ></span>
                }
                checkedIcon={
                  <span
                    className="iconify"
                    data-icon="noto-v1:sun-with-face"
                    data-inline="false"
                    style={{
                      display: "block",
                      height: "100%",
                      fontSize: 25,
                      textAlign: "end",
                      marginLeft: "10px",
                      color: "#353239",
                    }}
                  ></span>
                }
                id="icon-switch"
              />
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default Header;