import React from "react";

const CountriesVisited = ({ countries }) => {
  // Define mapping for country flag emojis
  const countryFlags = {
    Turkey: "🇹🇷",
    Greece: "🇬🇷",
    Bulgaria: "🇧🇬",
    Hungary: "🇭🇺",
    Slovakia: "🇸🇰",
    Austria: "🇦🇹",
    "Czech Republic": "🇨🇿",
    Germany: "🇩🇪",
    Netherlands: "🇳🇱",
    Ireland: "🇮🇪",
    Belgium: "🇧🇪",
    France: "🇫🇷",
    Spain: "🇪🇸",
    Italy: "🇮🇹",
    Morocco: "🇲🇦",
    "United States": "🇺🇸",
  };

  return (
    <section id="countries-visited" style={{ textAlign: "center", padding: "30px 0", backgroundColor: "#1F1F1F" }}>
      <h2 style={{ fontSize: "2.5em", fontWeight: "bold", color: "#ffff", marginBottom: "40px" }}>Countries I've Visited</h2>
      <div style={{ display: "flex", flexWrap: "wrap", justifyContent: "center", gap: "15px" }}>
        {countries.map((country) => (
          <div
            key={country}
            style={{
              display: "flex",
              alignItems: "center",
              padding: "12px 20px",
              borderRadius: "8px",
              boxShadow: "0 4px 10px rgba(0, 0, 0, 0.15)",
              backgroundColor: "#ffffff",
              transition: "transform 0.3s ease, box-shadow 0.3s ease",
              cursor: "pointer",
              fontSize: "1.1em"
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.transform = "scale(1.05)";
              e.currentTarget.style.boxShadow = "0 6px 12px rgba(0, 0, 0, 0.2)";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.transform = "scale(1)";
              e.currentTarget.style.boxShadow = "0 4px 10px rgba(0, 0, 0, 0.15)";
            }}
          >
            <span style={{ fontSize: "1.5em", marginRight: "10px" }}>
              {countryFlags[country] || "🏳️"}
            </span>
            <span style={{ fontWeight: "500", color: "#555" }}>{country}</span>
          </div>
        ))}
      </div>
    </section>
  );
};

export default CountriesVisited;