import React, { useEffect, useState } from "react";

const VisionMission = ({ visionMission }) => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    // Simulate a fade-in effect by updating visibility after component mounts
    setTimeout(() => setIsVisible(true), 100);
  }, []);

  if (!visionMission) return null;

  return (
    <section
      id="vision-mission"
      style={{
        padding: "30px 10px",
        backgroundColor: "#f4f4f4",
        color: "#333",
        textAlign: "center",
      }}
    >
      <h1
        className="section-title"
        style={{
          fontSize: "2.5em",
          fontWeight: "bold",
          marginBottom: "40px",
          color: "#2c3e50",
        }}
      >
        Vision and Mission
      </h1>

      <div style={{ maxWidth: "900px", margin: "0 auto" }}>
        {/* Vision Section */}
        <div
          className={`vision ${isVisible ? "fade-in" : ""}`}
          style={{
            backgroundColor: "#ffffff",
            padding: "30px",
            borderRadius: "10px",
            marginBottom: "30px",
            boxShadow: "0 8px 16px rgba(0, 0, 0, 0.2)",
            opacity: 0,
            transition: "opacity 1s ease",
          }}
        >
          <span role="img" aria-label="target" style={{ fontSize: "2.5em" }}>🎯</span> {/* Vision emoji */}
          <h3 style={{ fontSize: "2em", fontWeight: "bold", color: "#333", marginTop: "15px" }}>Vision</h3>
          <p style={{ fontSize: "1.6em", color: "#555", lineHeight: "1.8", marginTop: "15px" }}>
            {visionMission.vision}
          </p>
        </div>

        {/* Mission Section */}
        <div
          className={`mission ${isVisible ? "fade-in" : ""}`}
          style={{
            backgroundColor: "#ffffff",
            padding: "30px",
            borderRadius: "10px",
            boxShadow: "0 8px 16px rgba(0, 0, 0, 0.2)",
            opacity: 0,
            transition: "opacity 1s ease",
          }}
        >
          <span role="img" aria-label="rocket" style={{ fontSize: "2.5em" }}>🚀</span> {/* Mission emoji */}
          <h3 style={{ fontSize: "2em", fontWeight: "bold", color: "#333", marginTop: "15px" }}>Mission</h3>
          <p style={{ fontSize: "1.6em", color: "#555", lineHeight: "1.8", marginTop: "15px" }}>
            {visionMission.mission}
          </p>
        </div>
      </div>
    </section>
  );
};

export default VisionMission;