import React from "react";

const CertificationsCourses = ({ certifications }) => {
  if (!certifications) return null;

  return (
    <section id="certifications" style={{ padding: "40px 0", backgroundColor: "#f9f9f9" }}>
      <h1 className="section-title text-center" style={{ color: "#333", fontSize: "2.2em", marginBottom: "30px" }}>
        Certifications and Courses
      </h1>
      <div style={{ maxWidth: "600px", margin: "0 auto", position: "relative", paddingLeft: "30px" }}>
        {certifications.map((cert, index) => (
          <div key={index} style={{ position: "relative", marginBottom: "20px", textAlign: "left" }}>
            {/* Dot and Line */}
            <div
              style={{
                position: "absolute",
                left: "0",
                top: "10px",
                width: "10px",
                height: "10px",
                backgroundColor: "#333",
                borderRadius: "50%",
              }}
            ></div>
            {index < certifications.length - 1 && (
              <div
                style={{
                  position: "absolute",
                  left: "4px",
                  top: "30px",
                  width: "2px",
                  height: "100%",
                  backgroundColor: "#ccc",
                }}
              ></div>
            )}
            {/* Certification Details */}
            <div style={{ marginLeft: "30px" }}>
              <h3 style={{ fontSize: "1.2em", color: "#333", marginBottom: "5px" }}>{cert.title}</h3>
              {cert.organization && (
                <p style={{ fontSize: "1em", color: "#666", marginBottom: "2px" }}>{cert.organization}</p>
              )}
              <p style={{ fontSize: "0.9em", color: "#999" }}>{cert.year}</p>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default CertificationsCourses;