import React, { Component } from "react";

class About extends Component {
  render() {
    // Extract data from props
    const sectionName = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.section_name.about
      : "";
    const hello = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.description_header
      : "";
    const about = this.props.resumeBasicInfo
      ? this.props.resumeBasicInfo.description
      : "";
    const socialLinks = this.props.sharedBasicInfo
      ? this.props.sharedBasicInfo.social
      : [];

    // Split the description text into paragraphs
    const aboutParagraphs = about ? about.split('\n\n') : [];

    return (
      <section id="about" style={{ padding: "60px 0", backgroundColor: "#f9f9f9" }}>
        <div className="container text-center">
          {/* Section Heading */}
          <h1 className="section-title mb-4" style={{ fontSize: "2.5em", fontWeight: "bold" }}>
            {sectionName}
          </h1>

          {/* Introduction */}
          <p className="greeting-text" style={{ fontSize: "1.6em", marginBottom: "20px", color: "#333" }}>
            {hello} :)
          </p>

          {/* About Description */}
          <div className="about-description" style={{ maxWidth: "800px", margin: "0 auto", fontSize: "1.6em", color: "#555", lineHeight: "1.8" }}>
            {aboutParagraphs.map((paragraph, index) => (
              <p key={index} style={{ marginBottom: "1em" }}>
                {paragraph}
              </p>
            ))}
          </div>

          {/* Social Media Links */}
          <div className="social-icons mt-4" style={{ display: "flex", justifyContent: "center", gap: "20px" }}>
            {socialLinks.map((link, index) => (
              <a
                key={index}
                href={link.url}
                target="_blank"
                rel="noopener noreferrer"
                style={{ fontSize: "1.5em", color: "#333" }}
              >
                <i className={link.class}></i>
              </a>
            ))}
          </div>
        </div>
      </section>
    );
  }
}

export default About;