import React, { Component } from "react";
import ProjectDetailsModal from "./ProjectDetailsModal";

class Projects extends Component {
  constructor(props) {
    super(props);
    this.state = {
      deps: {},
      detailsModalShow: false,
    };
  }

  render() {
    const detailsModalShow = (data) => {
      this.setState({ detailsModalShow: true, deps: data });
    };

    const detailsModalClose = () => this.setState({ detailsModalShow: false });

    let sectionName = "";
    let projects = null;

    // Check if resumeProjects and resumeBasicInfo are available
    if (this.props.resumeProjects && this.props.resumeBasicInfo) {
      sectionName = this.props.resumeBasicInfo.section_name.projects; // Should display "News Articles"
      
      // Sort projects by date from newest to oldest
      const sortedProjects = [...this.props.resumeProjects].sort((a, b) => new Date(b.date) - new Date(a.date));

      // Map sorted projects to display project data
      projects = sortedProjects.map((project) => (
        <div
          className="col-sm-12 col-md-6 col-lg-4"
          key={project.title}
          style={{
            cursor: "pointer",
            marginBottom: "30px",
            padding: "15px",
          }}
        >
          <span className="portfolio-item d-block">
            <div
              className="foto"
              onClick={() => detailsModalShow(project)}
              style={{
                padding: "15px",
                backgroundColor: "#ffffff",
                borderRadius: "12px",
                boxShadow: "0 8px 16px rgba(0, 0, 0, 0.15)",
                textAlign: "center",
                transition: "transform 0.3s ease, box-shadow 0.3s ease",
              }}
              onMouseEnter={(e) => {
                e.currentTarget.style.transform = "scale(1.05)";
                e.currentTarget.style.boxShadow = "0 12px 20px rgba(0, 0, 0, 0.2)";
              }}
              onMouseLeave={(e) => {
                e.currentTarget.style.transform = "scale(1)";
                e.currentTarget.style.boxShadow = "0 8px 16px rgba(0, 0, 0, 0.15)";
              }}
            >
              <img
                src={project.image}
                alt="projectImages"
                style={{
                  width: "100%",
                  height: "400px", // Fixed height for all images
                  objectFit: "cover",
                  objectPosition: "center",
                  borderRadius: "8px",
                  marginBottom: "10px",
                }}
              />
              <span className="project-date" style={{ fontSize: "0.9em", color: "#777" }}>
                {project.date}
              </span>
              <h4
                className="project-title-settings mt-2"
                style={{
                  fontSize: "1.1em",
                  fontWeight: "600",
                  color: "#333",
                  marginTop: "10px",
                  marginBottom: "0",
                }}
              >
                {project.title}
              </h4>
            </div>
          </span>
        </div>
      ));
    }

    return (
      <section id="portfolio">
        <div className="col-md-12">
          <h1 className="section-title" style={{ color: "black", fontSize: "2.5em", fontWeight: "bold", marginBottom: "40px" }}>
            <span>{sectionName}</span>
          </h1>
          <div className="col-md-12 mx-auto">
            <div className="row mx-auto">{projects}</div>
          </div>
          <ProjectDetailsModal
            show={this.state.detailsModalShow}
            onHide={detailsModalClose}
            data={this.state.deps}
          />
        </div>
      </section>
    );
  }
}

export default Projects;