import React from 'react';

const ChampionshipsCourses = ({ championships_courses }) => {
  if (!championships_courses || championships_courses.length === 0) {
    return null; // Return nothing if data is not available
  }

  return (
    <section className="section-container" id="championships-courses">
      <h1 className="section-title">Organized Events</h1>
      <div className="content">
        {championships_courses.map((course, index) => (
          <div key={index} className="course-card" style={{ marginBottom: '40px', padding: '20px', border: '1px solid #ddd', borderRadius: '10px' }}>
            {/* Title and Description */}
            <h3>{course.title}</h3>
            <p>
              {course.date && <span>{course.date}</span>}
              {course.location && <span>, {course.location}</span>}
            </p>
            {course.description && <p>{course.description}</p>}
            
            {/* Image grid */}
            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '10px', marginTop: '15px' }}>
              {course.images && course.images.map((image, imgIndex) => (
                <img
                  key={imgIndex}
                  src={image}
                  alt={`${course.title} ${imgIndex + 1}`}
                  style={{
                    width: '100%',
                    height: '200px',
                    objectFit: 'center',
                    borderRadius: '8px'
                  }}
                />
              ))}
            </div>
          </div>
        ))}
      </div>
    </section>
  );
};

export default ChampionshipsCourses;